import React, { useState } from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { OrderStatus } from '../../../../types/OrderStatus';

interface IOrderItemStatusButtons {
  orderStatus: OrderStatus,
}

const OrderItemStatusButtons = ({ orderStatus }: IOrderItemStatusButtons): React.ReactElement => {
  const [editMode, setEditMode] = useState<boolean>(false);

  return (
    <ButtonGroup>
      {orderStatus.name === 'Новые' && (
        <>
          <Button size="sm" variant="outline-primary">Подтвердить</Button>
          <Button size="sm" variant="outline-danger">Отменить</Button>
        </>
      )}
    </ButtonGroup>
  );
};

export default OrderItemStatusButtons;
